@import url(https://fonts.googleapis.com/css2?family=Cabin:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding: 0;
}
html{
  scroll-behavior: smooth;
  
}
.nav nav{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

nav ul{
  display: flex;
}

nav ul li{
  margin: 20px 20px;
  list-style: none;
  font-size: 18px;
}
nav ul li a{
  text-decoration: none;
  color: rgb(255, 255, 255);
}
.main{
  width: 100%;
  height: 100vh;
  background: url(/sanskritijain05/cofounder.git/static/media/main.707efe1b.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.container{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  margin-top: 100px;
}


.heading h1{
  font-size: 80px;
  color: white;
  text-align: left;
  padding: 30px;

}
.heading h1 strong{
  background: linear-gradient(to right, #ff008c, #9900ff);
  -webkit-background-clip: text;
          background-clip: text;
  color:  transparent;
}
.heading h3{
  font-size: 26px;
  color: white;
  font-weight: 600;

  line-height: 50px;
  padding: 30px;
}

.heading button{
  padding: 15px;
  width: 200px;
  font-size: 21px;
  border-radius: 12px;
  border: none;
  background-image: linear-gradient(to right, #9900ff, #ff006a);
  margin: 30px;
}
.heading button a{
  text-decoration: none;
  color: white;
  font-weight: 600;
}
.image{
  margin-left: 70px;
}

#hiw{
  width: 100%;
  height: -webkit-max-content;
  height: max-content;
}

@media screen and (max-width:760px) {
  .image{
    margin-left: 0px;
  }
}

.cards{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;

}

.card{
  width: 400px;
  height: 200px;
  background: rgb(255, 255, 255);
  margin: 10px;
  border-left:  2px solid black;


}

.card h1{
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  text-align: center;
  color: #2c2b3b;
  margin-top: 60px;
  font-size: 40px;
}

.heading-2{
  text-align: center;
  padding: 100px;
  font-size: 40px;
  background: linear-gradient(to right, #2b2b2b , #ac6cff);
  -webkit-background-clip: text;
          background-clip: text;
  color:  transparent;
}

.cofounder{
  width: 100%;
  height:  -webkit-max-content;
  height:  max-content;
  background: url(/sanskritijain05/cofounder.git/static/media/main.707efe1b.png);
}
.cofounder h1{
  text-align: center;
  color: white;
  padding: 40px;
  font-size: 70px;
}

.cofounder h1 strong{
  background: linear-gradient(to right, #7700ff, #9900ff);
  -webkit-background-clip: text;
          background-clip: text;
  color:  transparent;
}

.card2{

  width: 600px;
  height: 360px;
  background: transparent;

}

.cards2{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
margin-top: 40px;

}

#card-4, #card-3{
  border-top: 4px solid white;
  border-image-slice: 1;
}


#card-3, #card-1{
  border-right: 4px solid white;
  border-image-slice: 1;
}

.card2 h1{
  font-size: 40px;
  text-align: left;
  background: linear-gradient(to right, #9a41ff, #ff009d);
  -webkit-background-clip: text;
          background-clip: text;
  color:  transparent;
  font-weight: 600;
}


.card2 h2{
  font-size: 22px;
  text-align: left;
  font-weight: 500;
  color: white;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  line-height: 40px;
}

@media screen and (max-width: 1200px) {
  .main{
    height: 110vh;
  }
  .image img{
  display: none;
  }
  .heading-2 h1{
    font-size: 50px;
  }
  .card{
    border-left: none;
    border-bottom: 2px solid black;
  }
  .cofounder{
    width: 100%;
    height:  -webkit-max-content;
    height:  max-content;
    background: url(/sanskritijain05/cofounder.git/static/media/main.707efe1b.png);
  }
  
#card-4, #card-3, #card-1, #card-2{
  border: none
}

.card2 h1{
  font-size: 25px;

}
.heading h1{
  font-size: 60px;
  color: white;
  text-align: left;
  padding: 30px;

}
.heading h3{
  font-size: 20px;
  color: white;
  font-weight: 600;
  line-height: 50px;
  padding: 30px;
}
.card2 h2{
  font-size: 18px;
  text-align: left;
  font-weight: 500;
  color: white;
  width: 80%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: auto;
  line-height: 40px;
}
}

.testimonials{
  margin-top: 50px;
  width: 100%;
  height: 70vh;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  background: white;
}

.testimonials h1{
  font-size: 80px;
  text-align: center;
  padding: 40px;
  background: linear-gradient(to right, #252525, #0004ff);
  -webkit-background-clip: text;
          background-clip: text;
  width: 30%;
  margin: auto;
  color:  transparent;
}
.test-cards{
  margin: auto;
  width: 60%;
}
.cards3{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
  margin: auto;
}
.card3{
  display: flex;
  flex-direction: column;
  width: 360px;
  height: 230px;
  background: #2c2b3b;
  border-radius: 20px;
  margin: 20px;
}
.left-image{
  width: 100%;
  display: flex;
  flex-direction: row;
  padding: 20px;
  justify-content: space-evenly;
}

.left-image img{
  width: 60px;
  height: 60px;
  border-radius: 50%;
}
.left-image h2{
  color: white;
  font-size: 23px;
  font-weight: 500;
}
.left-image p{
  color: white;
  font-weight: 400;
}
.right-content{
  display: flex;
  margin: 30px;
  margin-top: -10px;
  color: white;
}
.right-content p{
  margin-top: 10px;
  font-size: 16px;
}


@media screen and (max-width: 700px) {
  .right-content{
    display: flex;
    margin: 10px;
    margin-top: -10px;
  }
  .left-image h2{
    color: white;
    font-size: 19px;
    font-weight: 500;
  }
  .left-image p{
    color: white;
    font-weight: 400;
    font-size: 14px;
  }
  .right-content p{
    font-size: 16px;
  }
  .testimonials h1{
    font-size: 60px;
    text-align: center;
    padding: 40px;
    background: linear-gradient(to right, #252525, #0004ff);
    -webkit-background-clip: text;
            background-clip: text;
    width: 80%;
    margin: auto;
    color:  transparent;
  }
}

